html {
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
}
body {
	margin:0;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}
@media (prefers-color-scheme: dark) {
	body {
		background-color: rgba(48, 48, 48, 1.0);
		color: rgba(255, 255, 255, 1.0);
	}

}
*, *::before, *::after {
    box-sizing: inherit;
}
